<template>
  <q-page class="row items-center justify-center">
    <div class="col-xs-12 col-sm-10 col-md-3 q-pa-md">
    <q-card class="my-card">
      <img src="../assets/avatar/login/img0001.png">
      <q-card-section>
        <div class="text-h6 text-primary">Acesso Restrito.</div>
        <small class="text-primary">Esta área é exclusivamente de uso interno.</small>
      </q-card-section>

      <q-card-section class="q-pt-none">
        <div class="q-gutter-md">
          <q-input outlined v-model="email" label="Usuário" />

          <q-input outlined v-model="password" :type="isPwd ? 'password' : 'text'" hint="Digite sua senha.">
            <template v-slot:append>
              <q-icon
                :name="isPwd ? 'visibility_off' : 'visibility'"
                class="cursor-pointer"
                @click="isPwd = !isPwd"
              />
            </template>
          </q-input>
        </div>
      </q-card-section>

      <q-separator />

      <q-card-actions class="q-pa-md q-gutter-md" vertical>

<!-- accent secondary primary-->
          <q-btn
            @click="login()"
            color="primary"
            size="20px"
            :label="`Entrar`"
          />
        <q-btn size="sm" text-color="primary" flat>Esqueceu sua senha? Clique aqui</q-btn>
        
      </q-card-actions>
    </q-card>
    </div>
  </q-page>
</template>

<style>
.q-field__messages {
  color: #1d9490;
}
</style>

<script>
import { ref } from 'vue'

localStorage.clear()

const setup = {
  origin: process.env.VUE_APP_URL_API_ORIGIN,
  url: process.env.VUE_APP_URL_API_AUTH
};

export default {
  name: 'Login',
  setup () {
    return {
      setup: setup,
      user: [],
      token: null,
      password: ref(''),
      isPwd: ref(true),      
      email: ref(''),
      date: ref('')
    }
  },
  methods: {

    async login() {
      const data = {

        email: this.email,
        password: this.password

      };

      const dataJson = JSON.stringify(data);

      console.log(dataJson);

      const req = await fetch("https://clientes.test/invet/api/auth", {//"https://agpublick.com.br/invet/api/auth/"
        method: "POST",
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: {
          'Content-Type': 'application/json'
        },  
        redirect: 'follow',
        referrerPolicy: 'no-referrer',
        body: dataJson,
      });

      const res = await req.json();

      localStorage.setItem('user_token', res.data.token)
      localStorage.setItem('user', JSON.stringify(res.data.user))

      if(res.data !== 'Não autenticado'){
        this.token = localStorage.getItem('user_token');
        this.user = localStorage.getItem('user');
      } else {
        this.token = false
        localStorage.clear()
      }
      
      if(this.token){
        this.$router.push('/')
      } 

    }

},
mounted() {

}
}
</script>
